import * as React from 'react';
import classNames from 'classnames/dedupe';
import './tilfredshet-valg.less';
import {useState} from 'react';

interface TilfredshetValgProps {
    onTilfredshetChanged: (tilfredshet: number) => void;
    defaultTilfredshet?: number;
    className?: string;
}

function TilfredshetValg(props: TilfredshetValgProps) {
    const [tilfredshet, setTilfredshet] = useState(props.defaultTilfredshet ? props.defaultTilfredshet : 0);

    const handleTilfredshetChanged = (tilfredshet: number) => {
        setTilfredshet(tilfredshet);
        props.onTilfredshetChanged(tilfredshet);
    };

    const hentKlasserForIkon = (ikonTilfredshet: number): any => {
        const erValgt = ikonTilfredshet === tilfredshet;
        const harValgt = tilfredshet > 0;
        return classNames('tilfredshet-valg__ikon', {
            'tilfredshet-valg__ikon--valgt': erValgt,
            'tilfredshet-valg__ikon--ikke-valgt': harValgt && !erValgt
        });
    };

    return (
        <div className={classNames('tilfredshet-valg__wrapper', props.className)}>
            <img
                alt="Veldigt lite tilfreds"
                className={hentKlasserForIkon(1)}
                onClick={() => handleTilfredshetChanged(1)}
                src="data:img/png;base64,"
                data-testid="tilfredshet-ikon_1"
            />
            <img
                alt="Lite tilfreds"
                className={hentKlasserForIkon(2)}
                onClick={() => handleTilfredshetChanged(2)}
                src="data:img/png;base64,"
                data-testid="tilfredshet-ikon_2"
            />
            <img
                alt="Helt ok tilfreds"
                className={hentKlasserForIkon(3)}
                onClick={() => handleTilfredshetChanged(3)}
                src="data:img/png;base64,"
                data-testid="tilfredshet-ikon_3"
            />
            <img
                alt="Tilfreds"
                className={hentKlasserForIkon(4)}
                onClick={() => handleTilfredshetChanged(4)}
                src="data:img/png;base64,"
                data-testid="tilfredshet-ikon_4"
            />
            <img
                alt="Veldigt tilfreds"
                className={hentKlasserForIkon(5)}
                onClick={() => handleTilfredshetChanged(5)}
                src="data:img/png;base64,"
                data-testid="tilfredshet-ikon_5"
            />
        </div>
    );
}

export default TilfredshetValg;
